<template>
  <div class="classify">
    <van-tabs
      v-model="index"
      title-inactive-color="#999"
      animated
      swipeable
      color="lightsteelblue"
      title-active-color="steelblue"
      sticky
    >
      <van-tab :title="v.name" class="box clearfix" v-for="(v,i) in nameList" :key="i">
        <div
          class="classify-list fl"
          v-for="(item , index) in classifyData[v.enname]"
          :key="index"
          @click="goCategory(v.enname,item.name)"
        >
          <div class="v-title">{{item.name}}</div>
          <div class="v-msg">书籍数量 : {{item.bookCount}}</div>
        </div>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
export default {
  data() {
    return {
      index: 0,
      classifyData: {},
      nameList: [
        { name: "男生", enname: "male" },
        { name: "女生", enname: "female" },
        { name: "出版", enname: "press" },
        { name: "漫画", enname: "picture" }
      ]
    };
  },
  created() {
    this.getClassify();
  },
  methods: {
    // 获取分类数据
    getClassify() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });
      this.axios({
        method: "get",
        url: "http://novel.kele8.cn/categories"
      })
        .then(result => {
          this.$toast.clear();
 
          if (result.status == 200) {
            this.classifyData = result.data;
          }
        })
        .catch(err => {
          this.$toast.clear();
 
        });
    },
    // 图片解码
    getImg(val) {
      return unescape(val).slice(7);
    },
    // 跳转到小分类
    goCategory(gender, major) {
      this.$router.push({
        name: "Category",
        query: { gender: gender, major: major }
      });
    }
  }
};
</script>

<style lang="less" scoped>
.classify {
  position: fixed;
  height: 100%;
  width: 100%;
  overflow-y: auto;
  &::-webkit-scrollbar {
    display: none;
  }
  .box {
    height: 100%;
  }
  .classify-list {
    height: 100%;
    width: calc(~"50% - 10px");
    text-align: center;
    padding: 10px;
    box-sizing: border-box;
    box-shadow: 1px 1px 3px #ddd;
    border-radius: 10px;
    margin: 5px;
    background-image: linear-gradient(to bottom right, #eee, #fff, #fff);
    .v-title {
      font-size: 20px;
      color: steelblue;
    }
    .v-msg {
      color: lightsteelblue;
    }
  }
}
</style>